import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import './Details.css';

function Details() {
    let params = useParams();
    const [Details, setDetails] = useState({});
    let baseImgurl = 'https://image.tmdb.org/t/p/original/';

     function GetDetails() {
        return axios.get(`https://api.themoviedb.org/3/${params.mediatype}/${params.id}?api_key=c636ed7787cc302d96bf88ccf334e0d8&language=en-US`);

    }
    useEffect(() => {
        let isMounted = true;       
        GetDetails().then(resp => {
            

          if (isMounted) setDetails(resp.data);    // add conditional check
        })
        return () => { isMounted = false }; // cleanup toggles value, if unmounted
      }, []);  

 

    return (
        <div className=" Details py-5">
            <div className="row py-5">


                <div className="col-md-4">
                    <img className="w-100" src={Details.poster_path?baseImgurl + Details.poster_path:'' }alt="" />
                </div>



                <div className="col-md-8">
                    <div className="p-4">
                        {params.mediatype === 'movie' ? <h3 className="fw-light">{Details.title}</h3> : <h3 className="fw-light">{Details.name}</h3>}

                        <p className="secondary-Font-color fs-4 fw-light">{Details.tagline}</p>

                        {Details.genres?Details.genres.map((g , index)=> <span key={index} className="me-2 movie-type p-2 rounded">{g.name}</span>):''}


                        <h6 className="fw-light my-4">Vote : {Details.vote_average}</h6>
                        <h6 className="fw-light my-4">Vote Count : {Details.vote_count}</h6>
                        <h6 className="fw-light my-4">Popularity : {Details.popularity}</h6>

                        {params.mediatype === 'movie' ? <h6 className="fw-light my-4">Release Date : {Details.release_date}</h6> :
                            <h6 className="fw-light my-4">Release Date : {Details.first_air_date}</h6>}

                        <button className="fw-light btn btn-outline-info my-3"><a href={Details.homepage}> Click to Watch</a></button>

                        <p className="secondary-Font-color  fs-4 fw-light mt-3">{Details.overview}</p>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Details;