import React, { useContext } from "react";
import { MediaContext } from "../../Components/MediaContext/MediaContext";

function People(){

    let baseImgurl ='https://image.tmdb.org/t/p/original/';
    let {TrendingPeople}= useContext(MediaContext);
    
    return(

        <div className=" Movies py-5">
        <div className=" row py-5 gy-5">
            
            <div className="col-md-4 d-flex align-items-center">
                <div className="w-100"> 
                <div className="line w-25"></div>
                    <h2 className="mt-3">Trending<br/> Actors<br/> to watch now</h2>
                    <p className="secondary-Font-color">most Trending Actors All week</p>
                <div className="line mt-3"></div>
                </div>
            </div>
      

        {TrendingPeople.map((person,index)=> 
            <div className="col-md-2" key={index}>
                <img className="w-100 " src={baseImgurl+person.profile_path} alt="" />
              <h5 className="mt-3">{person.name}</h5>
                <h6 className="fw-light secondary-Font-color">Known For:</h6>
              {person.known_for.map((title,index)=>
              <p key={index} className="fw-light">- {title.title}</p>)}
            </div>
        )}
          </div>
        </div>
    )

}

export default People;