import { createContext, useState ,useEffect } from "react";
import axios from "axios";



export let MediaContext = createContext([]);

export function MediaContextProvider(props){

    const [trendingMovies,setTrendingMovies] = useState([]);
    const [trendingTv,setTrendingTv] = useState([]);
    const [Movies,setMovies]=useState([]);
    const [Tvshows,setTvshows] = useState([]) ;
    const [TrendingPeople,setTrendingPeople] = useState([]) ;

    async function getItems(mediaType,CallBack,time){

        let {data}= await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/${time}?api_key=c636ed7787cc302d96bf88ccf334e0d8`);

        CallBack(data.results);
        }

    useEffect( ()=>{
        getItems('movie',setTrendingMovies,'day');
        getItems('tv',setTrendingTv,'day');
        getItems('movie',setMovies,'week');
        getItems('tv',setTvshows,'week');
        getItems('person',setTrendingPeople,'week');
        
    },[])

    return <MediaContext.Provider value={{trendingMovies,trendingTv,Movies,Tvshows,TrendingPeople}}>

        {props.children}
    </MediaContext.Provider>
}