import logo from './logo.svg';
import './App.css';
import NavBar from './Components/NavBar/NavBar';
import { Navigate, Route, Routes, useNavigate } from 'react-router-dom';
import Register from './Pages/Registration/Register';
import LogIn from './Pages/LogIn/LogIn';
import Home from './Pages/Home/Home';
import People from './Pages/People/People';
import TV from './Pages/TvShow/TV';
import Movies from './Pages/Movies/Movies';
import jwtDecode from 'jwt-decode';
import { Children, useEffect, useState } from 'react';
import Details from './Components/Details/Details';
import { MediaContextProvider } from './Components/MediaContext/MediaContext';

function App() {

  const [userData,setUserData] = useState(null);

  let navigate= useNavigate();

  function getUserData(){
   let token = jwtDecode(localStorage.getItem('userToken'))
    setUserData(token);
    
  
  }

  // useEffect(()=>{console.log(userData)},[userData]); // as useState is async


  function logOut(){

    localStorage.removeItem('userToken');
    setUserData(null);
    navigate('/login');
  }

 useEffect(()=>{
    if(localStorage.getItem('userToken')){
      getUserData();
    }

 },[])


 function ProtectedRoute({children}){

   if(!localStorage.getItem('userToken'))
   {
     return <Navigate to='/login'/>
   }

   else{
     return children;
   }
 }

  return (
    <div>
    <NavBar userData={userData} logOut={logOut}/>
    <div className='container'>
      <MediaContextProvider>
    <Routes>

      <Route path='/' element={<LogIn/>}/>
      <Route path='/register' element={<Register/>}/>

      <Route path='/login' element={<LogIn getUserData={getUserData}/>}/>

      <Route path='/home' element={ <ProtectedRoute><Home/></ProtectedRoute>}/>
      <Route path='/movies' element={<ProtectedRoute><Movies/></ProtectedRoute>}/>
      <Route path='/tv' element={<ProtectedRoute><TV/></ProtectedRoute>}/>
      <Route path='/people' element={<ProtectedRoute><People/></ProtectedRoute>}/>

      <Route path='/details' element={<ProtectedRoute><Details/></ProtectedRoute>}>
        <Route path=':id/:mediatype' element={<ProtectedRoute><Details/></ProtectedRoute>}/>
        
      </Route>
      
      {/* <Route path='*' element={<>not found</>}/> */}
      

    </Routes>
    </MediaContextProvider>
    </div>
    </div>
  );
}

export default App;
